import React from 'react';
import { ShoppingCartProps } from '../../types';
import { useCart } from '../../hooks/useCart';
import { CartItem } from '../CartItem';
import { Button } from '../Button';
import { Card } from '../Card';
import { Modal } from '../Modal';
import './ShoppingCart.css';

export const ShoppingCart: React.FC<ShoppingCartProps> = ({
    onCheckout,
    onItemUpdate,
    onItemRemove,
    showCheckoutButton = true,
    showClearButton = true,
    maxItems,
    className = '',
}) => {
    const {
        items,
        removeItem,
        updateQuantity,
        clearCart,
        getTotalPrice,
        getTotalItems,
    } = useCart();

    const handleUpdateQuantity = (id: string | number, quantity: number) => {
        updateQuantity(id, quantity);
        const item = items.find(item => item.id === id);
        if (item && onItemUpdate) {
            onItemUpdate({ ...item, quantity });
        }
    };

    const handleRemoveItem = (id: string | number) => {
        removeItem(id);
        if (onItemRemove) {
            onItemRemove(id);
        }
    };

    const handleCheckout = () => {
        if (onCheckout) {
            onCheckout(items);
        }
    };

    const handleClearCart = () => {
        clearCart();
    };

    const isMaxItemsReached = maxItems ? getTotalItems() >= maxItems : false;

    if (items.length === 0) {
        return (
            <Card className={`shopping-cart empty ${className}`} padding="large">
                <div className="empty-cart">
                    <div className="empty-cart-icon">üõí</div>
                    <h3>Your cart is empty</h3>
                    <p>Add some items to get started!</p>
                </div>
            </Card>
        );
    }

    return (
        <div className={`shopping-cart ${className}`}>
            <Card className="cart-header" padding="medium">
                <div className="cart-summary">
                    <h2>Shopping Cart</h2>
                    <div className="cart-stats">
                        <span className="item-count">
                            {getTotalItems()} {getTotalItems() === 1 ? 'item' : 'items'}
                        </span>
                        <span className="total-price">
                            Total: ${getTotalPrice().toFixed(2)}
                        </span>
                    </div>
                </div>

                {showClearButton && (
                    <Button
                        variant="outline"
                        size="small"
                        onClick={handleClearCart}
                    >
                        Clear Cart
                    </Button>
                )}
            </Card>

            <div className="cart-items">
                {items.map((item) => (
                    <CartItem
                        key={item.id}
                        item={item}
                        onUpdateQuantity={handleUpdateQuantity}
                        onRemove={handleRemoveItem}
                        showImage={true}
                        showDescription={true}
                    />
                ))}
            </div>

            {isMaxItemsReached && (
                <Card className="max-items-warning" padding="medium">
                    <p>‚ö†Ô∏è Maximum number of items reached ({maxItems})</p>
                </Card>
            )}

            {showCheckoutButton && (
                <Card className="cart-footer" padding="medium">
                    <div className="checkout-section">
                        <div className="checkout-total">
                            <span className="total-label">Total:</span>
                            <span className="total-amount">${getTotalPrice().toFixed(2)}</span>
                        </div>

                        <Button
                            variant="primary"
                            size="large"
                            onClick={handleCheckout}
                            className="checkout-button"
                        >
                            Proceed to Checkout
                        </Button>
                    </div>
                </Card>
            )}
        </div>
    );
};
