import React from 'react';
import { CartItem as CartItemType } from '../../types';
import { Button } from '../Button';
import { Card } from '../Card';
import './CartItem.css';

export interface CartItemProps {
    item: CartItemType;
    onUpdateQuantity: (id: string | number, quantity: number) => void;
    onRemove: (id: string | number) => void;
    showImage?: boolean;
    showDescription?: boolean;
    className?: string;
}

export const CartItem: React.FC<CartItemProps> = ({
    item,
    onUpdateQuantity,
    onRemove,
    showImage = true,
    showDescription = false,
    className = '',
}) => {
    const handleQuantityChange = (newQuantity: number) => {
        if (newQuantity <= 0) {
            onRemove(item.id);
        } else if (item.maxQuantity && newQuantity > item.maxQuantity) {
            onUpdateQuantity(item.id, item.maxQuantity);
        } else {
            onUpdateQuantity(item.id, newQuantity);
        }
    };

    const handleIncrement = () => {
        const newQuantity = item.quantity + 1;
        handleQuantityChange(newQuantity);
    };

    const handleDecrement = () => {
        const newQuantity = item.quantity - 1;
        handleQuantityChange(newQuantity);
    };

    const totalPrice = item.price * item.quantity;

    return (
        <Card className={`cart-item ${className}`} padding="medium">
            <div className="cart-item-content">
                {showImage && item.image && (
                    <div className="cart-item-image">
                        <img src={item.image} alt={item.name} />
                    </div>
                )}

                <div className="cart-item-details">
                    <h4 className="cart-item-name">{item.name}</h4>

                    {showDescription && item.description && (
                        <p className="cart-item-description">{item.description}</p>
                    )}

                    {item.category && (
                        <span className="cart-item-category">{item.category}</span>
                    )}

                    <div className="cart-item-price">
                        <span className="price-per-unit">${item.price.toFixed(2)} each</span>
                        <span className="price-total">${totalPrice.toFixed(2)} total</span>
                    </div>

                    {item.inStock === false && (
                        <span className="cart-item-out-of-stock">Out of stock</span>
                    )}
                </div>

                <div className="cart-item-controls">
                    <div className="quantity-controls">
                        <Button
                            variant="outline"
                            size="small"
                            onClick={handleDecrement}
                            disabled={item.quantity <= 1}
                        >
                            -
                        </Button>

                        <span className="quantity-display">{item.quantity}</span>

                        <Button
                            variant="outline"
                            size="small"
                            onClick={handleIncrement}
                            disabled={item.maxQuantity ? item.quantity >= item.maxQuantity : false}
                        >
                            +
                        </Button>
                    </div>

                    <Button
                        variant="danger"
                        size="small"
                        onClick={() => onRemove(item.id)}
                    >
                        Remove
                    </Button>
                </div>
            </div>
        </Card>
    );
};
